
$shadow: 0px 1px 5px rgba(0,0,0,0.3);
$color1: rgba(138, 113, 106, 1);
$color2: rgba(194, 184, 178, 1);
$color3: rgba(25, 123, 189, 1);
$color4: rgba(18, 94, 138, 1);
$color5: rgba(32, 75, 87, 1);

* {
    padding: 0;
    margin: 0;
    font-family: 'Sarabun', sans-serif;
    color: $color5
}



.text-center {
    text-align: center;
}

.about {
    &-heading {
        padding: 10px;
        color: white;
    }

    &-subheading {
        padding: 5px;
        color: white;
        font-weight: 300;
    }
}

.nav {
    background: rgba(255, 255, 255, 0.9);
    position: fixed;
    height: 40px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;

    a {
        text-transform: uppercase;
        font-weight: 300;
        letter-spacing: 2px;
        font-size: 14px;
        padding: 20px;
        color: $color5;
        text-decoration: none;
    }
}

.container {
    width: 1200px;
    margin: auto;
}

.section-full {
    min-height: 100vh;
    width: 100%;
}

.content-centered {
    display: flex;
    align-items: center;
    justify-content: center;
}

.bg-gradient-1 {
    background: linear-gradient(to right,$color3, $color4)
}

.bg-gradient-2 {

}

.bg-gradient-3 {

}


.profile-image {
    margin: auto;
    height: 250px;
    width: 250px;
    background: white;
    border-radius: 50%;
    box-shadow: $shadow;
}

.tech-grid {
    margin: auto;
    width: 500px;
    margin-top: 40px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    align-items: start;
    align-content: start;
    justify-content: center;
    justify-items: center;
}

.technologies-list {
    list-style-type: none;
    
    li {
        padding: 5px;
        text-align: center;
    }
}

.skills-grid {
    display: grid;
    grid-gap: 10px;
    margin: auto;
    margin-top: 40px;
    margin-bottom: 40px;
    width: 500px;
    grid-template-columns: 1fr 1fr;
    
    div {
        border-radius: 2px;
        box-shadow: $shadow;
        padding: 20px;

        h3{
            padding-bottom: 10px;
            font-size: 17px;
        }

        p {
            font-size: 14px
        }

    }
}